import {
  fetchGif
} from '../../services/gifService';

import * as types from './actionTypes';

const gifStarted = () => {
  return {
    type: types.GIF_REQUESTED,
  };
}

const gifError = () => {
  return {
    type: types.GIF_ERROR,
  };
}

const gifFinished = () => {
  return {
    type: types.GIF_FINISHED,
  };
}

const setGif = (gif) => {
  return {
    type: types.SET_GIF,
    payload: gif,
  };
}

export const getGif = (url) => {
  return function (dispatch) {
    dispatch(gifStarted());
    return fetchGif(url).then((gif) => {
      dispatch(setGif(gif));
      dispatch(gifFinished())
      return gif;
    },
    (error) => {
      dispatch(gifError())
    })
  }
}



